.intro{
    display: flex;
    height: 77vh;
    margin-top: 6rem;
}
.i-left{
    display: flex;
    position: relative;
    flex: 1;
    flex-direction: column;
    gap: 2rem;
}
.i-name{
    display: flex;
    flex-direction: column;
}
.i-name>:nth-child(1){
    color: var(--black);
    font-weight: bold;
    font-size: 3rem;
}
.i-name>:nth-child(2){
    color: var(--orange);
    font-weight: bold;
    font-size: 3rem;
}
.i-name>:nth-child(3){
    color: var(--gray);
    font-size: 14px;
    font-weight: 100;
    margin-top: 10px;
}
.i-link{
    display: flex;
    position: relative;
    flex: 1;
}
.i-right{
    flex: 1;
    position: relative;
    top: -6rem;
}

.i-list{
    flex: 10;
}
.i-list>ul{
    display: flex;
    gap: 0rem;
    margin-right: 4rem;
}
.i-button{
    width: 6rem;
    height: 2rem;
}
.i-link:hover{
    cursor: pointer;
}
.blur{
    position: absolute;
    width: 22rem;
    height: 14rem;
    border-radius: 50%;
    background: #edd0ff;
    filter: blur(212px);
    z-index: -9;
    top: -18%;
    left: 56%;
    filter: blur(72px);
}